.movie-list{
    padding: 1.5rem;
    border-radius: 4px;
    background-color:#fcfa;
    box-shadow: 15px 15px 30px rgba(0, 0, 0, 0.15);
    margin-top: 1.5rem;
}
.list-group-item{
    padding: 15px 20px;
    border-bottom: 1px solid #3d5a80;
}
.list-group-item:last-child{
    border-bottom:none;
}
.list-group-item span{
    line-height: 35px;
    font-size: 20px;
    cursor: pointer;
    width: 550px;
}
.list-group-item input{
    line-height: 35px;
    font-size: 20px;
    text-align: center;
    border: 0;
}
.list-group-item button{
    width: 35px;
    height: 35px;
    margin: 3px;
    font-size: 17px;
    border: none;
    cursor: pointer;
}
.list-group-item .btn-cookie{
    color:#e5383b;
    width: 50px;
}
.list-group-item .btn-trash{
    color: #ffd700;
    width: 50px;
}
.list-group-item .fa-star{
    width: 35px;
    height: 35px;
    text-align: center;
    line-height: 35px;
    font-size: 16px;
    color:#ffd700;
    transition: 0.3s all;
    transform: translateX(30px);
    opacity: 0;
}
.list-group-item.like .fa-star{
    opacity: 1;
    transform: translateX(0px);
}
.list-group-item.favourite .list-group-item-label,
.list-group-item.favourite .list-group-item-label{
    color:#e09f3e;
}
.list-group-item.favourite .btn-star{
    color:#aeaeae
}